@import url(http://fonts.googleapis.com/css?family=Montserrat);

* {
    margin: 0;
    padding: 0;
}

.loginContainer {
    min-height: 100vh;
    background: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.fieldGroup {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.inputGroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 500px;
}


.errorContainer {
    height: 6px;
    margin: 0;
    text-align: center;
}

.msform {
    margin: auto;
    color: white;
}

.msform fieldset {

    background: white;
    border: 0 none;
    border-radius: 10px;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.4);
    padding: 20px 30px;
    box-sizing: border-box;
    max-width: 600px;
    /* width: 100%; */
    margin: 0;

}

.msform input {
    padding: 10px 60px;
    width: 100%;

}

.msform label {
    color: grey;
    font-weight: bolder;
}

.sigIn {
    text-decoration: none;
    color: black;
}

.msform fieldset:not(:first-of-type) {
    display: none;
}

.msform input {
    border: 1px solid #ccc;
    border-radius: 3px;
    width: 400px;
    box-sizing: border-box;
    font-family: montserrat;
    color: #2C3E50;
    font-size: 13px;
    padding: 10px;
    margin-bottom: 10px;
    position: relative;
}

.inputGroup input::placeholder {
    text-align: left;
    direction: rtl;
}


.passwordContainer,
.emailContainer {
    display: flex;
    align-items: center;
}

.msform button {
    width: 100px;
    background: white;
    font-weight: bold;
    color: black;
    border: 0 none;
    border-radius: 1px;
    cursor: pointer;
    padding: 10px 5px;
    margin: 10px 5px;
    transition: background-color 0.3s ease;
}

.msform .buttonSigIn:hover,
#msform .buttonSigIn:focus {

    background: black;
    color: white;
}

.buttonSigIn a {
    text-decoration: none;
}

.buttonLogIn:enabled {

    background: black;
    color: white;
    transition: background-color 2s, color 2s;
    /* Definimos la transici√≥n para ambos atributos */
}

.buttonLogIn:disabled {
    background-color: #CCCCCC;
    color: #969393;
}


.fsTitle {
    font-size: 25px;
    font-weight: bold;
    text-transform: uppercase;
    color: #2C3E50;
    margin-bottom: 10px;
    text-align: center;
}

.divider {
    width: 100%;
    height: 1px;
    background-color: #e1dbdb;
    margin: 20px 0;
}


.forgotPsw {
    color: black;
    text-decoration: none;
    font-weight: bolder;
    font-size: 13px;
}

.passwordContainer {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.eyeIconContainer {
    position: absolute;
    right: 10px;
    top: 9px;
    cursor: pointer;
    color: rgb(108, 105, 105);
}

.bothButtons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;
}

.btn_google_container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 40px;
    margin: 10px 0;
}

.btn_google {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    font-size: 1.1em;
    border: 1px solid rgb(155, 155, 155);
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
}

.btn_google a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: rgb(77, 77, 77);
    text-decoration: none;
    padding-right: 10px;
}

.btn_google_logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 100%;
}

.btn_google_logo svg {
    height: 20px;
}